<template>
	<view class="">
		<view class="toptitle">
			<text>{{titleDesign||'未填写'}}</text>
		</view>
		<swiper v-if="Object.keys(DesignBanner).length!=0" :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item @tap='PreviewPic(item,DesignBanner)' v-for="(item,index) in DesignBanner" :key='index'>
				<view class="swiper-item">
					<image :src='item' mode="aspectFit"></image>
				</view>
			</swiper-item>	
		</swiper>
		<swiper v-else :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item>
				<view class="swiper-item">
					<image src='../../static/image/default.png' mode="aspectFit"></image>
				</view>
			</swiper-item>	
		</swiper>
		<view class="docInfor" v-for="(item,index) in docinfor" :key='index'>
			<image v-if="item.doctorVO.avatarLinks" :src='item.doctorVO.avatarLinks' mode=""></image>
			<image v-else src="../../static/image/shoyiilogo.png" mode=""></image>
			<view class="docInfor_content">
				<view class="">
					<text class="docname">{{item.doctorVO.name}}</text>
					<view class="infor">
						<text>{{item.doctorVO.department}}</text>
						<text class="line">|</text>
						<text>{{item.doctorVO.hospital}}</text>
					</view>
				</view>
				<view class="infor">
					<!-- <text>{{caseDesign.createTime}}</text> -->
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data(){
			return{
				Imageplay:true,//轮播图是否自动切换,true自动切换
			}
		},
		props:{
			titleDesign:{
				type:String,
				default:""
			},
			// 三维图形数据
			DesignBanner:{
				type:Object,
				default:()=>{
					return{}
				},
			},
			docinfor:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			// 病例详情的所有数据
			caseDesign:{
				type:Object,
				default:()=>{
					return{}
				},
			}
			
		},
		methods:{
			PreviewPic(){},
			check(){
				console.log(this.docinfor)
				console.log(this.DesignBanner)
				console.log(Object.keys(this.DesignBanner).length)
				console.log(this.caseDesign)
				console.log(this.titleDesign)
			},
		},
		created() {
		}
		
	}
</script>

<style scoped>
	.toptitle{
		padding: 26rpx 0;
		background-color: #FFFFFF;
		text-align: center;
	}
	.swiper-item image{
		width: 100%;
		height: 420rpx;
		background: #FFFFFF;
	}
	uni-swiper{
		height: 420rpx!important;
	}
	.docInfor{
		display: flex;
		padding: 30rpx 30rpx;
		align-items: flex-start;
		border-bottom: 1px solid #F0EFEF;
		background-color: #FFFFFF;
	}
	.docInfor uni-image{
		width: 100rpx;
		height: 100rpx;
		border-radius: 50%;
		border: 1px solid #F0EFEF;
		margin-right: 23rpx;
	
	}
	.docInfor .docname{
		font-size: 28rpx;
		color: #000000;
	}
	.docInfor_content{
		flex: 1;display: flex;justify-content: space-between;
	}
	.infor .line{
		margin: 0 10rpx;
	}
	.docInfor .infor{
		font-size: 24rpx;
		line-height: 70rpx;
		color: #8F8F8F;
	}
</style>
